<template>
    <div class="logindiv" style="width: 100%;height: 100%;">
        <div class="fcs_main">
            <div style="width: 100%;height: 100%;position:relative">
                <div class="_top">
                    <div class="imgdiv">
                    </div>
                </div>
                <div class="_form">
                    <h2>微警务后台管理登录</h2>
                   <label><img src="../../img/denglu_zhanghao_xuanzhong.png">
                   <el-autocomplete
                      :fetch-suggestions="querySearch"
                      style="width:200px;"
                      placeholder="请输入内容"
                      v-model="username"
                      @select="handleSelect">
                    </el-autocomplete></label>
                   <label><img src="../../img/denglu_mima_xuanzhong.png"><input v-model="password" type="password" @keyup.enter="denglu"></label>
                   <div v-show="is_login" class="f_tips" ref="_tips">请输入密码</div>
                   <button v-on:click="denglu">登陆</button>
                </div>
                <img class="imgdiv02" src="../../img/beijing2.png" alt="">
            </div>
        </div>
         <div class="_footer">
            <div><i></i><span>2017©company.华奥科技 版权所有</span></div>
        </div>
    </div>
</template>
<script>
import {short_md5} from '../../js/md5.js'
export default {
        data:function(){
            return{
                is_login:false,
                restaurants: [],
                password: "",
                username:""
                }
        },
        methods: {
            querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
          },
          createFilter(queryString) {
                return function(e){
                    return (e.value.indexOf(queryString.toLowerCase()) === 0);
                }
          },
          handleSelect(item) {
            console.log(item);
          },
        
        denglu:function() {
            var username=this.username;
            var pws=this.password;
            if (username=="") {
                this.$refs._tips.innerHTML="请输入用户名";
                this.is_login=true;
            }else if(pws==""){
                this.$refs._tips.innerHTML="请输入密码";
                this.is_login=true;
            }
            else{
                let _pwd =  short_md5(pws);
                this.$http.post(global.config.weburl+ 'api/user/login.do',{phone:username,password:_pwd},{emulateJSON:true})
                    .then(function(data){
                    var _data=data.data;
                    if(_data.code!=0){
                        this.$refs._tips.innerHTML=_data.msg;
                        this.is_login=true;
                    }else{
                        if ("backUser" == _data.data.user.type && "authed" == _data.data.user.auth) {
                            //console.log(_data.data.user);
                            global.data.setUserInfo(_data.data.user);
                            global.data.setToken(_data.data.token);
                            var arr=localStorage.getItem("username");
                            if(arr){
                                arr=JSON.parse(arr);
                                var step=0;
                                arr.forEach(function(value,index,array){
                                    if(value.value==username){
                                        return false;
                                    }
                                    step++;
                                })

                                if(step==arr.length){

                                    arr.push({"value":username})
                                }
                            }else{
                                arr=[];
                                arr.push({"value":username})
                            }
                            arr=JSON.stringify(arr)
                            localStorage.setItem("username", arr);
                            this.$router.push("/wlf");
                        } else {
                            this.$refs._tips.innerHTML="非后台认证用户";
                            this.is_login=true;
                        }

                    }
                },function(error){
                });
            } 
        }
    },
    mounted(){
        var usernamearr=localStorage.getItem("username");
        if(usernamearr){
            usernamearr=JSON.parse(usernamearr);
            this.restaurants=usernamearr;
        }else{
            this.restaurants=[];
        }
        

    }
}
</script>
<style src="../../css/login.css">
    
</style>




