<template>
    <el-col>
        <el-menu default-active="2" class="el-menu-vertical-demo">
        <el-menu-item v-for="obj in navstatus" :index="obj.index" @click="navchange($event)">
            <router-link :to='obj.link'><img v-show="obj.isshow" :src="obj.src01" alt="obj.name" /><img v-show="!obj.isshow" :src="obj.src02" alt="obj.name" />{{obj.name}}</router-link></el-menu-item>
    </el-menu>
</el-col>
</template>

<script>
export default {
    name: 'nav',
    data:function(){
        return {
            wlf:false,
            wfw:false,
            wxx:false,
            navstatus:[
                {index:"2",isshow:false,link:"/wlf",src01:"../../src/img/icon_weilianfang_xuanzhong.png",src02:"../../src/img/icon_weilianfang.png",name:"微联防"},
                {index:"3",isshow:false,link:"/navs",src01:"../../src/img/icon_weifuwu_xuanzhong.png",src02:"../../src/img/icon_weifuwu.png",name:"微服务"},
                {index:"4",isshow:false,link:"/wxx",src01:"../../src/img/icon_weixinxi_xuanzhong.png",src02:"../../src/img/icon_weixinxi.png",name:"微信息"},
                {index:"5",isshow:false,link:"/jsz",src01:"../../src/img/icon_weiredian_xuanzhong.png",src02:"../../src/img/icon_weiredian.png",name:"微热点"},
                {index:"6",isshow:false,link:"/whd",src01:"../../src/img/icon_weihudong_xuanzhong.png",src02:"../../src/img/icon_weihudong.png",name:"微互动"},
                {index:"7",isshow:false,link:"/wsj",src01:"../../src/img/icon_weishuju_xuanzhong.png",src02:"../../src/img/icon_weishuju.png",name:"微数据"},
                {index:"8",isshow:false,link:"/jsz",src01:"../../src/img/icon_weifenxi_xuanzhong.png",src02:"../../src/img/icon_weifenxi.png",name:"微分析"}
            ]
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        navchange(e){
            this.navstatus.forEach(function(value,index){
                if(index+2 ==e.index){
                    value.isshow=true;
                }else{
                    value.isshow=false;
                }
            })

        }
    },
    created(){
        this.navstatus[0].isshow=true;
    }
}
</script>
<style scoped>
a, .is-opened .router-link-active{color: rgb(72, 87, 106);}
.is-active .router-link-active{color:#428bca;}
.el-submenu .el-menu-item{padding: 0;}
.el-menu-item-group__title{display: none;}
.el-menu-item a{display: block;width: 100%;font-size: 18px;}

.el-menu-item a:hover, .el-menu-item a:focus, .el-menu-item a:visited{text-decoration:none;}
.el-menu-item:hover{background: transparent!important;}

.el-menu-item a:active{color: rgb(77,162,193)!important;}
.el-menu-vertical-demo .el-menu-item, .el-submenu__title{height: 60px;line-height: 60px;}
.el-menu-item a img{width: 26px;height: 26px;float: left;margin-top: 17px; margin-right: 30px;}



</style>


