<template>
<div class="index-page">
    <header class="header">
        <el-row class="cs-header-top">
            <el-col :span="16" class='logo-container'>
                <img class="cs-web-logo" src="../../img/logo.png"></img><span class="cs-web-text">&nbsp;微警务运营平台</span>
            </el-col>
            <el-col :span="8" class="userinfo">
                <img class="cs-user-logo" src="../../img/userlogo.png"></img><span class="cs-user-name">{{realname}}</span><span v-on:click="exitSystem" class="cs-btn-exit">退出</span>
            </el-col>
        </el-row>
    </header>

    <div class="main-container cs-index-container" >
        <div class="main-container-inner">
            <!--<a class="menu-toggler" id="menu-toggler" href="#">
                <span class="menu-text"></span>
            </a>-->
            <div class="sidebar" id="sidebar" :style="leftnavstyle">
                <sidebar ref="sidebar"></sidebar>
            </div>
        </div>
        <router-view></router-view>

    </div>


</div>

</template>

<script>
import Sidebar from './nav.vue';

export default {

    name: 'index',
    data:function() {
        return {
            is_show_per_menu: false,
            realname: "",
            leftnavstyle:{}
        }
    },
    methods:{
        showPerMenu:function() {
            this.is_show_per_menu = true;
            console.log("showPerMenu");
        },
        exitweb :function () {
            this.is_show_per_menu = false;
            console.log("退出登录");
        },
        exitSystem : function() {
            console.log("退出");
            sessionStorage.clear();
            this.$router.push("/");
        }
    },
    components: {
        sidebar:Sidebar
    },
    created(){
        var that = this;
        that.realname = global.data.getUserInfo() ? global.data.getUserInfo().realname: "";
        let _leftnavheight = document.documentElement.offsetHeight  - 68;
        that.leftnavstyle = {
            height: _leftnavheight +"px"
        };
    }

}
</script>







