import login from '../pages/base/login.vue'
import indexvue from '../pages/base/index.vue'

export default [
    { 
        path: "/", 
        component: login
    },
    {
        path: "/index", 
        component: indexvue,

        children:[
            {
                path:'/wlf',
                component:function (resolve, reject) {
                    require(['../pages/wlf/main.vue'], resolve)
                }
            },
            {
                path:'/szyx',
                component:function (resolve, reject) {
                    require(['../pages/wfw/szyx.vue'], resolve)
                }
            },
            {
                path:'/bszn',
                component:function (resolve, reject) {
                    require(['../pages/wfw/bszn.vue'], resolve)
                }
            },
            {
                path:'/vedio',
                component:function (resolve, reject) {
                    require(['../pages/wfw/vedio.vue'], resolve)
                }
            },
            {
                path:'/wxx',
                component:function (resolve, reject) {
                    require(['../pages/wxx/main.vue'], resolve)
                }
            },
            {
                path:'/szxx',
                component:function (resolve, reject) {
                    require(['../pages/wfw/szxx.vue'], resolve)
                }
            },
            {
            	path:'/navs',
            	component:function (resolve, reject) {
                    require(['../pages/wfw/navs.vue'], resolve)
                }
            },
            {
                path:'/smrz',
                component:function (resolve, reject) {
                    require(['../pages/wfw/smrz.vue'], resolve)
                }
            },
            {
                path:'/jsz',
                component:function (resolve, reject) {
                    require(['../pages/base/jianshezhong.vue'], resolve)
                }
            },
            {
                path:'/whd',
                component:function (resolve, reject) {
                    require(['../pages/whd/whdnav.vue'], resolve)
                }
            },
            {
                path:'/fzpzsxc',
                component:function (resolve, reject) {
                    require(['../pages/whd/fzp.vue'], resolve)
                }
            },
            {
                path:'/zxfw',
                component:function (resolve, reject) {
                    require(['../pages/whd/zxfw.vue'], resolve)
                }
            },
            {
                path:'/wsj',
                component:function (resolve, reject) {
                    require(['../pages/wsj/wsj.vue'], resolve)
                }
            }
        ]
    }
    
]





