import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './router/router'
import moment  from 'moment'
//import axios  from 'axios'
import VideoPlayer from 'vue-video-player'
import VueResource from 'vue-resource'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'


Vue.use(ElementUI);
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.config.debug = false;
Vue.config.devtools = false;


Vue.http.options.headers = {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'};
Vue.http.options.emulateJSON = true;
Vue.http.options.crossOrigin = true;
Vue.http.options.emulateHTTP = true;


//Vue.prototype.$http = axios;
//Vue.prototype.$http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';



Vue.filter('moment', function (value, formatString) {
    //console.log(value);
    if(!value){return "";}
    formatString = formatString || 'YYYY-MM-DD HH:mm';
    return moment(value).format(formatString);
});

var App = Vue.extend({});
var router = new VueRouter({
    routes
})

var app = new App({
    router
}).$mount("#base-main-content");




global.eventHub = new Vue();

global.config = {
    weburl: "http://www.huaaotech.tech/",
    imgbaseurl:"http://www.huaaotech.tech/file/down/file.do?path="
    //weburl: "http://192.168.7.237/"
};

global.data = {
    //token : function (){
    //    return sessionStorage.getItem("ha_ao_token");
    //},
    getUserInfo: function () {
        var user_str =  sessionStorage.getItem("ha_ao_userinfo")
        return JSON.parse(user_str);
    },
    setUserInfo: function(userinfo) {
        //JSON.parse(userinfo);
        var userinfo_str = JSON.stringify(userinfo);
        sessionStorage.setItem("ha_ao_userinfo", userinfo_str);
    },
	getToken : function (){
        var _token = sessionStorage.getItem("ha_ao_token");
        if (!_token) {
            router.push("/");
            return null;
        }
        return _token;
	},
	setToken : function (token) {
        sessionStorage.setItem("ha_ao_token", token);
	}
};
global.tools = {
    handleError : function (data) {
        if (data.code == 501) {
            app.$message({
                type: 'error',
                message: "请重新登陆"
            });
            router.push("/");
        } else {
            app.$message({
                type: 'error',
                message: data.msg
            });
        }
    },
    startEjingwu:function() {
    	var isWin = (window.navigator.platform == "Win32") || (navigator.platform == "Windows");
    	if(!isWin){
    		app.$message({type: 'error', message: "视频会议需要安装微警务客户端，请使用Windows操作系统，谢谢！"});
    		return false;
    	}else if(window.ActiveXObject){
    		var obj = new ActiveXObject("EJingwuV2");
    		if(obj == null || obj == undefined){
    			app.$message({type: 'error', message: "您尚未安装微警务客户端，请先安装客户端程序后重试，谢谢！"});
    			return false;
    		}else{
    			window.location = 'EJingwuV2://'+global.data.getToken();
    			return true;
    		}
    	}else{
            window.open('EJingwuV2://'+global.data.getToken());
    		//app.$message({type: 'warning', message: "视频会议需要安装微警务客户端。如果您尚未安装，请先安装客户端程序后重试，谢谢！"});
    		return true;
    	}
    }
};

Array.prototype.remDub = Array.prototype.remDub || function () {
    var res = [];
    var json = {};
    for(var i = 0; i < this.length; i++){
        if(!json[this[i]]){
            res.push(this[i]);
            json[this[i]] = 1;
        }
    }
    return res;
    //return [...new Set(this)];
};







